import React, { useState } from 'react';
import { useLocation } from 'react-router-dom';
import Sidebar from './Sidebar';
import { useAuth } from '../context/AuthContext';

const pageTitles = {
  '/dashboard': { title: '', highlight: '' },
  '/books': { title: '', highlight: '' },
  '/sales': { title: '', highlight: '' },
  '/rentals': { title: '', highlight: '' },
  '/customers': { title: '', highlight: '' },
  '/expenses': { title: '', highlight: '' },
  '/employees': { title: '', highlight: '' },
  '/assets': { title: '', highlight: '' },
  '/debts': { title: '', highlight: '' },
  '/accounting': { title: '', highlight: '' },
  '/analytics': { title: '', highlight: '' },
};

export default function Layout({ children }) {
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const { user } = useAuth();
  const location = useLocation();
  const page = pageTitles[location.pathname] || { title: '', highlight: '' };

  return (
    <div className="layout">
      <Sidebar open={sidebarOpen} onClose={() => setSidebarOpen(false)} />
      <div className="main-content">
        <header className="top-header">
          <div style={{ display: 'flex', alignItems: 'center', gap: 12 }}>
            <button className="menu-toggle" onClick={() => setSidebarOpen(true)}></button>
            <h1 className="page-title">{page.title} <span>{page.highlight}</span></h1>
          </div>
          <div className="header-actions">
            <div className="header-user">
              <div className="avatar">{user?.username?.[0]?.toUpperCase()}</div>
              <span>{user?.username}</span>
            </div>
          </div>
        </header>
        <div className="page-wrapper">{children}</div>
      </div>
    </div>
  );
}
