import React, { useState, useEffect } from 'react';
import api from '../api/axios';

export default function Accounting() {
  const [summary, setSummary] = useState(null);
  const [loading, setLoading] = useState(true);
  const [exporting, setExporting] = useState(false);

  useEffect(() => {
    api.get('/analytics/summary').then(r => setSummary(r.data)).finally(() => setLoading(false));
  }, []);

  const fmt = (n) => (n || 0).toLocaleString('ar-EG', { maximumFractionDigits: 2 });

  const exportExcel = async () => {
    setExporting(true);
    try {
      const res = await api.get('/export/excel', { responseType: 'blob' });
      const url = window.URL.createObjectURL(new Blob([res.data]));
      const a = document.createElement('a');
      a.href = url; a.download = `__${new Date().toLocaleDateString('en-GB').replace(/\//g, '-')}.xlsx`;
      a.click(); window.URL.revokeObjectURL(url);
    } catch (err) { alert('    '); }
    finally { setExporting(false); }
  };

  if (loading) return <div className="loading-wrapper"><div className="spinner"></div></div>;

  const netProfit = summary?.netProfit || 0;

  const Section = ({ title, rows, color }) => (
    <div style={{ marginBottom: 6 }}>
      <div style={{ padding: '10px 16px', background: 'var(--bg-dark)', fontSize: 13, fontWeight: 700, color, borderRight: `3px solid ${color}` }}>{title}</div>
      {rows.map(([label, val, valColor]) => (
        <div key={label} className="summary-row">
          <span className="label" style={{ paddingRight: 12 }}>{label}</span>
          <span className="value" style={{ color: valColor || 'var(--text-primary)' }}>{val}</span>
        </div>
      ))}
    </div>
  );

  return (
    <>
      <div className="page-header">
        <h1> <span></span></h1>
        <button className="btn btn-primary" onClick={exportExcel} disabled={exporting}>
          {exporting ? '  ...' : '  Excel'}
        </button>
      </div>

      <div className="two-col-grid" style={{ marginBottom: 24 }}>
        {/* Income Statement */}
        <div className="card">
          <h3 style={{ marginBottom: 16, color: 'var(--accent)', fontSize: 16, fontWeight: 800 }}>  </h3>
          <Section title="" color="var(--success)" rows={[
            [' ', `${fmt(summary?.revenue?.sales)} `, 'var(--success)'],
            [' ', `${fmt(summary?.revenue?.rentals)} `, 'var(--success)'],
            [' ', `${fmt(summary?.revenue?.lateFees)} `, 'var(--warning)'],
          ]} />
          <div className="summary-row" style={{ borderTop: '1px solid var(--border)', marginTop: 4 }}>
            <span className="label" style={{ fontWeight: 700 }}> </span>
            <span className="value" style={{ color: 'var(--success)', fontWeight: 800, fontSize: 16 }}>{fmt(summary?.revenue?.total)} </span>
          </div>

          <div style={{ marginTop: 12 }}>
            <Section title="" color="var(--danger)" rows={[
              [' ', `${fmt(summary?.expenses?.operations)} `, 'var(--danger)'],
              [' ', `${fmt(summary?.expenses?.salaries)} `, 'var(--danger)'],
            ]} />
            <div className="summary-row" style={{ borderTop: '1px solid var(--border)', marginTop: 4 }}>
              <span className="label" style={{ fontWeight: 700 }}> </span>
              <span className="value" style={{ color: 'var(--danger)', fontWeight: 800, fontSize: 16 }}>{fmt(summary?.expenses?.total)} </span>
            </div>
          </div>

          <div style={{ marginTop: 16, padding: '16px', borderRadius: 10, background: netProfit >= 0 ? 'rgba(39,174,96,0.1)' : 'rgba(231,76,60,0.1)', border: `1px solid ${netProfit >= 0 ? 'rgba(39,174,96,0.3)' : 'rgba(231,76,60,0.3)'}` }}>
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
              <span style={{ fontWeight: 800, fontSize: 16 }}>  </span>
              <span style={{ fontWeight: 900, fontSize: 24, color: netProfit >= 0 ? 'var(--success)' : 'var(--danger)' }}>{fmt(netProfit)} </span>
            </div>
          </div>
        </div>

        {/* Other Items */}
        <div style={{ display: 'flex', flexDirection: 'column', gap: 16 }}>
          <div className="card">
            <h3 style={{ marginBottom: 16, color: 'var(--accent)', fontSize: 16, fontWeight: 800 }}>  </h3>
            <div className="summary-row">
              <span className="label">  </span>
              <span className="value" style={{ color: 'var(--warning)' }}>{fmt(summary?.pending?.unpaidSales)} </span>
            </div>
            <div className="summary-row">
              <span className="label">  </span>
              <span className="value" style={{ color: 'var(--warning)' }}>{fmt(summary?.pending?.unpaidRentals)} </span>
            </div>
            <div className="summary-row">
              <span className="label">  </span>
              <span className="value" style={{ color: 'var(--danger)' }}>{fmt(summary?.pending?.pendingLateFees)} </span>
            </div>
            <div className="summary-row total">
              <span className="label"> </span>
              <span className="value" style={{ color: 'var(--warning)', fontSize: 18 }}>
                {fmt((summary?.pending?.unpaidSales || 0) + (summary?.pending?.unpaidRentals || 0) + (summary?.pending?.pendingLateFees || 0))} 
              </span>
            </div>
          </div>

          <div className="card">
            <h3 style={{ marginBottom: 16, color: 'var(--accent)', fontSize: 16, fontWeight: 800 }}>  </h3>
            <div className="summary-row">
              <span className="label">  ( )</span>
              <span className="value" style={{ color: 'var(--success)' }}>{fmt(summary?.debts?.owedToMe)} </span>
            </div>
            <div className="summary-row">
              <span className="label">  ( )</span>
              <span className="value" style={{ color: 'var(--danger)' }}>{fmt(summary?.debts?.owedByMe)} </span>
            </div>
            <div className="summary-row total">
              <span className="label">  </span>
              <span className="value" style={{ color: (summary?.debts?.owedToMe - summary?.debts?.owedByMe) >= 0 ? 'var(--success)' : 'var(--danger)', fontSize: 16 }}>
                {fmt((summary?.debts?.owedToMe || 0) - (summary?.debts?.owedByMe || 0))} 
              </span>
            </div>
          </div>

          <div className="card">
            <h3 style={{ marginBottom: 16, color: 'var(--accent)', fontSize: 16, fontWeight: 800 }}>  </h3>
            <div className="summary-row"><span className="label"> </span><span className="value badge badge-info">{summary?.stats?.activeRentals || 0}</span></div>
            <div className="summary-row"><span className="label"> </span><span className="value badge badge-danger">{summary?.stats?.overdueRentals || 0}</span></div>
            <div className="summary-row"><span className="label"> </span><span className="value">{summary?.stats?.totalSales || 0} </span></div>
            <div className="summary-row"><span className="label"> </span><span className="value">{summary?.stats?.totalRentals || 0} </span></div>
          </div>
        </div>
      </div>

      <div className="card" style={{ textAlign: 'center', padding: '32px' }}>
        <div style={{ fontSize: 48, marginBottom: 12 }}></div>
        <h3 style={{ fontSize: 18, fontWeight: 800, marginBottom: 8 }}>  </h3>
        <p style={{ color: 'var(--text-secondary)', fontSize: 14, marginBottom: 20 }}>  Excel   5 :      </p>
        <button className="btn btn-primary" onClick={exportExcel} disabled={exporting} style={{ fontSize: 16, padding: '14px 40px' }}>
          {exporting ? '  ...' : '   Excel '}
        </button>
      </div>
    </>
  );
}
