import React from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

const navItems = [
  { section: '', items: [
    { path: '/dashboard', icon: '', label: ' ' },
    { path: '/analytics', icon: '', label: '' },
    { path: '/accounting', icon: '', label: ' ' },
  ]},
  { section: '', items: [
    { path: '/books', icon: '', label: '' },
    { path: '/sales', icon: '', label: '' },
    { path: '/rentals', icon: '', label: '' },
    { path: '/customers', icon: '', label: '' },
  ]},
  { section: '', items: [
    { path: '/expenses', icon: '', label: '' },
    { path: '/employees', icon: '', label: '' },
    { path: '/assets', icon: '', label: ' ' },
    { path: '/debts', icon: '', label: '' },
  ]},
];

export default function Sidebar({ open, onClose }) {
  const { user, logout } = useAuth();
  const navigate = useNavigate();

  const handleLogout = () => { logout(); navigate('/login'); };

  return (
    <>
      <div className={`sidebar-overlay ${open ? 'open' : ''}`} onClick={onClose} />
      <aside className={`sidebar ${open ? 'open' : ''}`}>
        <div className="sidebar-logo">
          <div className="logo-icon"></div>
          <div className="logo-text">
            <h2></h2>
            <span>  </span>
          </div>
        </div>

        <nav className="sidebar-nav">
          {navItems.map(section => (
            <div key={section.section}>
              <div className="nav-section-title">{section.section}</div>
              {section.items.map(item => (
                <NavLink
                  key={item.path}
                  to={item.path}
                  className={({ isActive }) => `nav-item ${isActive ? 'active' : ''}`}
                  onClick={onClose}
                >
                  <span className="nav-icon">{item.icon}</span>
                  <span>{item.label}</span>
                </NavLink>
              ))}
            </div>
          ))}
        </nav>

        <div className="sidebar-footer">
          <div style={{ fontSize: 12, color: 'var(--text-muted)', marginBottom: 10, textAlign: 'center' }}>
             <strong style={{ color: 'var(--accent)' }}>{user?.username}</strong>
          </div>
          <button className="logout-btn" onClick={handleLogout}>
            <span></span>  
          </button>
        </div>
      </aside>
    </>
  );
}
